import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpClient, HttpParams, HttpHeaders, HttpResponse, HttpEvent } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class HttpService {

  apiUrl = environment.apiUrl;

  constructor(
    private http: HttpClient,
  ) { }

  /**
   * GET API
   * @param path [string]
   * @param params [HttpParams | {[param: string]: string | string[]}]
   * @param header [HttpHeaders]
   */
  get(
    path: string,
    params: HttpParams | {[param: string]: string | string[]},
    header: HttpHeaders = new HttpHeaders()
    ): Observable<any> {
    return this.http.get(`${this.apiUrl}${path}`, { headers: header, params, observe: 'response' })
      .pipe(map(this.responseHandlerBody));
  }

  download(
    path: string,
    params: HttpParams | {[param: string]: string | string[]},
    header: HttpHeaders = new HttpHeaders()) {
      return this.http.get(`${this.apiUrl}${path}`,
       { headers: header, params, observe: 'response', responseType: 'blob' })
      .pipe(map(this.responseHandlerBody));
  }

  /**
   *   response
   * @param path [string]
   */
  getArrayBuffer(path: string): Observable<any> {
    return this.http.get(`${this.apiUrl}${path}`, { observe: 'response' , responseType: 'arraybuffer' });
  }

  /**
   * POST API
   * @param path [string]
   * @param body [any]
   * @param header [HttpHeaders]
   */
  post(path: string, body: any, header: HttpHeaders = new HttpHeaders()): Observable<any> {
      return this.http.post(`${this.apiUrl}${path}`, body, { headers: header, observe: 'response' })
      .pipe(map(this.responseHandlerBody));
  }

  /**
   * Post Api Event
   * @param path [string]
   * @param body [FormData]
   * @param header [HttpHeaders]
   */
  postEvent(path: string, body: FormData, header: HttpHeaders = new HttpHeaders()): Observable<HttpEvent<Object>> {
    return this.http.post(`${this.apiUrl}${path}`, body, { headers: header, observe: 'events', reportProgress: true });
  }

  /**
   * PUT API
   * @param path [string]
   * @param body [any]
   * @param header [HttpHeaders]
   */
  put(path: string, body: any, header: HttpHeaders = new HttpHeaders()): Observable<any> {
    return this.http.put(`${this.apiUrl}${path}`, body, { headers: header, observe: 'response' })
      .pipe(map(this.responseHandlerBody));
  }

  /**
   * Put Api Event
   * @param path [string]
   * @param body [any]
   * @param header [HttpHeaders]
   */
  putEvent(path: string, body: any, header: HttpHeaders = new HttpHeaders()): Observable<HttpEvent<Object>> {
    return this.http.put(`${this.apiUrl}${path}`, body, { headers: header, observe: 'events', reportProgress: true });
  }

  /**
   * DELETE API
   * @param path [string]
   * @param params [HttpParams | {[param: string]: string | string[]}]
   */
  delete(
    path: string,
    params: HttpParams | {[param: string]: string | string[]},
    header: HttpHeaders = new HttpHeaders()
    ): Observable<any> {
    return this.http.delete(`${this.apiUrl}${path}`, { headers: header, params, observe: 'response' })
      .pipe(
        map(this.responseHandlerBody)
      );
  }

  private responseHandlerBody(res: HttpResponse<any>) {
    // console.log('API:', res);
    return res.body;
  }

  /**
   *  http status 
   * @param statusNum number (200 404 ...)
   */
  private transferHttpStatus(statusNum: number): string {
    switch (statusNum) {
      case 200:
        return ';';
      case 204:
        return ';';
      case 400:
        return '';
      case 401:
        return '';
      case 403:
        return '';
      case 404:
        return '';
      case 405:
        return 'Http ';
      case 408:
        return '';
      case 431:
        return '';
      case 500:
        return '';
      case 505:
        return 'XXX';
      default:
        return '';
    }
  }

}
