import { Injectable } from '@angular/core';
import { AuthService } from './auth.service';
import { StorageService } from './storage.service';
import { HttpService } from '../services/http.service';
import { Router } from '@angular/router';
import { tap, catchError } from 'rxjs/operators';
import { of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SignInOutService {

  constructor(
    private router: Router,
    private authService: AuthService,
    private storageService: StorageService,
    private httpService: HttpService
  ) { }

  /**
   * 
   * 1.  localStorage 
   * 2.  API  ( guard canActivate  observer)
   */
  getUserInfo() {
    // 
    const storageUser = this.storageService.getUserInfo();
    if (! storageUser) {
      this.signOut();
      return;
    }
    this.authService.saveUserInfo(storageUser);

    // API (return observer  guard )
    // return this.httpService.get('', null);
  }

  /**
   * 
   */
  SignIn(body): void {
    this.httpService.post('', body).subscribe(
      res => {
        this.authService.saveUserInfo(Object.assign({isSignIn: true}, res));
        // this.router.navigate(['management', 'zone']);
        this.router.navigateByUrl('/');
      }, error => {
        //  isSignIn  (null )
        this.authService.saveUserInfo({isSignIn: false});
        this.clearToLogin();
      }
    );
  }

  /**
   *  token ( getUserInfo  )
   */
  reSignIn() {
    const acc = ''; // this.authService.xxx
    return this.httpService.get('/', null).pipe(
      tap( res => {
        this.authService.saveUserInfo(Object.assign({ isSignIn: true, acc }, res));
      }),
      catchError(() => {
        // this.router.navigate(['login']);
        return of({ isSignIn: false });
      })
    );
  }

  /**
   * 
   */
  signOut() {
    this.clearToLogin();

    // call API 
    this.httpService.delete('', null).subscribe();
  }

  /**
   * 
   * @param id [string]
   * @param otp [string]
   * @param pd [sting]
   */
  resetPassword() {
    const params = {};
    return this.httpService.put('/', params);
  }

  getEnPassword(id: string, pd: string) {
    // return this.passwordShaBase();
  }

  /**  id + password   sha256   base64
   * @param pd 
   */
  private passwordShaBase(): string {
    return '';
  }

  /**
   * 
   */
  private clearToLogin() {
    this.authService.clearUserInfo();
    this.router.navigateByUrl('/login');
  }
}
