import { printConsole } from 'src/app/core/services/common.service';
import { Component, OnInit, Inject, ViewChild, AfterViewInit, ElementRef, OnDestroy } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { NgForm, FormControl, NgModel } from '@angular/forms';
import * as moment from 'moment';
import videojs from 'video.js';
import youtube from 'videojs-youtube';

@Component({
  selector: 'app-video-player',
  templateUrl: './video-player.component.html',
  styleUrls: ['./video-player.component.css']
})
export class VideoPlayerComponent implements OnInit, AfterViewInit, OnDestroy {

  @ViewChild('target') target: ElementRef;
  private player: videojs.Player;

  set videoUrl(url: string) {
    this.player.controls(true); // 
    this.player.src(url);
  }

  set youtubeId(id: string) {
    this.player.controls(false); //  youtube
    const newYoutubeUrl: string = 'https://www.youtube.com/watch?v=' + id;
    this.player.src({ type: 'video/youtube', src: newYoutubeUrl });
  }

  constructor(
    public dialogRef: MatDialogRef<VideoPlayerComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) {}

  ngOnInit() {

  }

  ngAfterViewInit() {
    this.initVideoJs();
  }

  ngOnDestroy() {
    if (this.player) {
      this.player.dispose();
    }
  }

  private initVideoJs() {
    //  video-youtube  youtube 
    const excuteYoutube = youtube;

    this.player = videojs(this.target.nativeElement, {
      // controls: false,
      playsinline: true,
      fluid: true,
      muted: false, // true 
      autoplay: true,
      techOrder: ['html5', 'youtube'],
      youtube: {
        ytControls: true
      },
      // playbackRates: [2.0, 1.5, 1.2, 1.0, 0.7], // 
      controlBar: {
        fullscreenToggle: true, // true
        pictureInPictureToggle: false, // true
        volumePanel: true, // 
        // currentTimeDisplay: true, // 
        // timeDivider: true, // 
        // durationDisplay: true, // 
        // remainingTimeDisplay: false, // 
      },
    }, function onPlayerReady() {
      console.log('onPlayerReady', this);
    });

    this.player.controls(true); // 

    // './assets/WiccoError.mp4'
    // https://stream.hkxf.org:1935/sat/_definst_/pink.mp4/playlist.m3u8
    // https://stream.hkxf.org:1935/sat/_definst_/SAT Grammar 6-4.mp4/playlist.m3u8
    this.player.src(this.data ? this.data.url : './assets/WiccoError.mp4');

    // 
    this.player.currentTime(10);

    //   1
    this.player.volume(0.3);

    this.player.on('ready', function() {
      console.log('ready', this);
    });
    this.player.on('pause', function() {
      const whereYouAt = this.currentTime();
      const minutes = Math.floor(whereYouAt / 60);
      const seconds = Math.floor(whereYouAt - minutes * 60);
      const minutesText = minutes < 10 ? '0' + minutes : minutes;
      const secondsText = seconds < 10 ? '0' + seconds : seconds;
      console.log('pause', `${minutesText}:${secondsText}`);
    });
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

}
